.country {
  background-color: #34495e;
  color: aliceblue;
  display: flex;
  justify-content: center;
  padding: 1rem;
  align-items: center;
  border-radius: 0.6rem;
  transition: all 0.3s;
}
.country :hover {
  transform: scale(1.1);
}
.flag {
  width: 10rem;
  height: 7rem;
}
.btn {
  border: none;
  border-radius: 0.6rem;
  padding: 0.6rem;
  cursor: pointer;
  background-color: orange;
  transform: all 0.3s;
}
.btn :hover {
  background-color: red;
  color: aliceblue;
}
